import { Link, useParams } from 'react-router-dom';
import classnames from 'classnames';
import React, { useMemo } from 'react';
import { Test } from '../../types/Test';
import { Test1 } from '../Test1';
import { Test2 } from '../Test2';
import { Test3 } from '../Test3';
import { Test4 } from '../Test4';
import { Test5 } from '../Test5';

import './TestsPage.scss';

type Props = {
  tests: Test[],
};

export const TestsPage:React.FC<Props> = ({ tests }) => {
  const { testId } = useParams();

  const selectedTest = useMemo(
    () => (
      tests.find(test => test.id === testId)
    ),
    [testId],
  );

  return (
    <>
      <h1 className="tests-title title">Tests page</h1>

      <div className="tests-menu tabs is-boxed">
        <ul>
          {tests.map(test => (
            <li
              data-cy="Tab"
              className={classnames({
                'is-active': (
                  testId === test.id
                ),
              })}
              key={test.id}
            >
              <Link
                to={selectedTest
                  ? `../${test.id}`
                  : `./${test.id}`}
              >
                {test.title}
              </Link>
            </li>
          ))}
        </ul>
      </div>

      <div className="block" data-cy="TabContent">
        {selectedTest && selectedTest.title === 'Test 1' && <Test1 />}
        {selectedTest && selectedTest.title === 'Test 2' && <Test2 />}
        {selectedTest && selectedTest.title === 'Test 3' && <Test3 />}
        {selectedTest && selectedTest.title === 'Test 4' && <Test4 />}
        {selectedTest && selectedTest.title === 'Test 5' && <Test5 />}
        {!selectedTest && 'Please select a test'}
      </div>
    </>
  );
};
