import React, { useState } from 'react';
import classNames from 'classnames';
import ReactAudioPlayer from 'react-audio-player';
import audio1 from '../../audio/Test_01/L_0.mp3';
import audio2 from '../../audio/Test_01/R_0.mp3';
import audio3 from '../../audio/Test_01/L_-05.mp3';
import audio4 from '../../audio/Test_01/R_-05.mp3';

import audio5 from '../../audio/Test_01/L_-10.mp3';
import audio6 from '../../audio/Test_01/R_-10.mp3';
import audio7 from '../../audio/Test_01/L_-15.mp3';
import audio8 from '../../audio/Test_01/R_-15.mp3';
import audio9 from '../../audio/Test_01/L_-20.mp3';
import audio10 from '../../audio/Test_01/R_-20.mp3';
import audio11 from '../../audio/Test_01/L_-25.mp3';
import audio12 from '../../audio/Test_01/R_-25.mp3';
import audio13 from '../../audio/Test_01/L_-30.mp3';
import audio14 from '../../audio/Test_01/R_-30.mp3';
import audio15 from '../../audio/Test_01/L_-35.mp3';
import audio16 from '../../audio/Test_01/R_-35.mp3';
import noAudio1 from '../../audio/Test_01/No_audio_1.mp3';
import noAudio2 from '../../audio/Test_01/No_audio_2.mp3';
import noAudio3 from '../../audio/Test_01/No_audio_3.mp3';
import noAudio4 from '../../audio/Test_01/No_audio_4.mp3';

import './Test1.scss';

export const Test1:React.FC = () => {
  const test1 = [
    {
      audio: audio1,
      range: 0,
      options: ['L', 'R', 'Dont hear'],
      correct: 'L',
      choice: '',
    },
    {
      audio: audio2,
      range: 0,
      options: ['L', 'R', 'Dont hear'],
      correct: 'R',
      choice: '',
    },
    {
      audio: audio3,
      range: -5,
      options: ['L', 'R', 'Dont hear'],
      correct: 'L',
      choice: '',
    },
    {
      audio: audio4,
      range: -5,
      options: ['L', 'R', 'Dont hear'],
      correct: 'R',
      choice: '',
    },
    {
      audio: audio5,
      range: -10,
      options: ['L', 'R', 'Dont hear'],
      correct: 'L',
      choice: '',
    },
    {
      audio: audio6,
      range: -10,
      options: ['L', 'R', 'Dont hear'],
      correct: 'R',
      choice: '',
    },
    {
      audio: audio7,
      range: -15,
      options: ['L', 'R', 'Dont hear'],
      correct: 'L',
      choice: '',
    },
    {
      audio: audio8,
      range: -15,
      options: ['L', 'R', 'Dont hear'],
      correct: 'R',
      choice: '',
    },
    {
      audio: audio9,
      range: -20,
      options: ['L', 'R', 'Dont hear'],
      correct: 'L',
      choice: '',
    },
    {
      audio: audio10,
      range: -20,
      options: ['L', 'R', 'Dont hear'],
      correct: 'R',
      choice: '',
    },
    {
      audio: audio11,
      range: -25,
      options: ['L', 'R', 'Dont hear'],
      correct: 'L',
      choice: '',
    },
    {
      audio: noAudio1,
      range: 0,
      options: ['L', 'R', 'Dont hear1'],
      correct: 'Dont hear1',
      choice: '',
    },
    {
      audio: noAudio2,
      range: 0,
      options: ['L', 'R', 'Dont hear2'],
      correct: 'Dont hear2',
      choice: '',
    },
    {
      audio: audio12,
      range: -25,
      options: ['L', 'R', 'Dont hear'],
      correct: 'R',
      choice: '',
    },
    {
      audio: audio13,
      range: -30,
      options: ['L', 'R', 'Dont hear'],
      correct: 'L',
      choice: '',
    },
    {
      audio: audio14,
      range: -30,
      options: ['L', 'R', 'Dont hear'],
      correct: 'R',
      choice: '',
    },
    {
      audio: noAudio3,
      range: 0,
      options: ['L', 'R', 'Dont hear3'],
      correct: 'Dont hear',
      choice: '',
    },
    {
      audio: audio15,
      range: -35,
      options: ['L', 'R', 'Dont hear'],
      correct: 'L',
      choice: '',
    },
    {
      audio: audio16,
      range: -35,
      options: ['L', 'R', 'Dont hear'],
      correct: 'R',
      choice: '',
    },
    {
      audio: noAudio4,
      range: 0,
      options: ['L', 'R', 'Dont hear4'],
      correct: 'Dont hear',
      choice: '',
    },
  ];

  const [tests, setTests] = useState(test1);

  const handleChoiceChange = (audio: string, choice: string) => {
    const newArr = [...tests].map((test) => {
      if (test.audio === audio) {
        if (test.choice.localeCompare(choice) !== 0) {
          test.choice = choice;
        } else {
          test.choice = '';
        }
      }

      return test;
    });

    setTests(newArr);
  };

  const [isFinished, setIsFinished] = useState(false);
  const [score, setScore] = useState(0);
  const [res, setRes] = useState('');

  const handleSubmit = (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    const sumWithInitial = tests.reduce(
      (accumulator, cur) => {
        if (cur.choice === cur.correct) {
          return accumulator + 1;
        }

        return accumulator;
      },
      0,
    );
    // let left = 1;
    // let right = 1;
    const tempL = [10];
    const tempR = [10];

    for (const el of tests) {
      if (el.choice === el.correct && el.correct === 'L') {
        tempL.push(el.range);
      }

      if (el.choice === el.correct && el.correct === 'R') {
        tempR.push(el.range);
      }
    }

    setRes(`Ви чуєте лівим вухом при рівні ${Math.min(...tempL)} та правим ${Math.min(...tempR)} в залежності від звичайного голосу`);

    setScore(sumWithInitial);
    setIsFinished(true);
  };

  return (
    <>
      <h2>Test 1</h2>

      <form className="form" onSubmit={handleSubmit}>
        <div className="form_section">
          <h3>Прослухайте записи і позначте чи чуєте ви та який саме навушник</h3>
          {tests.map(test => (
            <div
              className={classNames(
                'form_section__question',
                { form_section__question__wrong: test.correct !== test.choice && isFinished },
                { form_section__question__right: test.correct === test.choice && isFinished },
              )}
              key={test.audio}
            >
              <ReactAudioPlayer
                src={test.audio}
                controls
                controlsList="play timeline"
              />

              <div className="form_section__question__answers">
                <div>
                  <input
                    type="radio"
                    id={`${test.audio}_${test.options[0]}`}
                    name={test.audio}
                    value={test.options[0]}
                    onClick={() => handleChoiceChange(test.audio, test.options[0])}
                    checked={test.choice === test.options[0]}
                    required
                  />
                  <label htmlFor={`${test.audio}_${test.options[0]}`}>{test.options[0]}</label>
                </div>

                <div>
                  <input
                    type="radio"
                    id={`${test.audio}_${test.options[1]}`}
                    name={test.audio}
                    value={test.options[1]}
                    onClick={() => handleChoiceChange(test.audio, test.options[1])}
                    checked={test.choice === test.options[1]}
                    required
                  />
                  <label htmlFor={`${test.audio}_${test.options[1]}`}>{test.options[1]}</label>
                </div>

                <div>
                  <input
                    type="radio"
                    id={`${test.audio}_${test.options[2]}`}
                    name={test.audio}
                    value={test.options[2]}
                    onClick={() => handleChoiceChange(test.audio, test.options[2])}
                    checked={test.choice === test.options[2]}
                    required
                  />
                  <label htmlFor={`${test.audio}_${test.options[2]}`}>{test.options[2]}</label>
                </div>
              </div>
            </div>
          ))}
        </div>
        <button
          type="submit"
          className="button"
        >
          Finish
        </button>

        {isFinished && (
          <div className="result">
            <h2>Results</h2>
            <h3>{`Your score: ${score}`}</h3>
            <p>{`Your result ${res}`}</p>
            <p>{`Your detailed result ${res}`}</p>
          </div>
        )}
      </form>
    </>
  );
};
